import React, { Component } from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';
import UserList from './user/UserList';
import UserDetail from './user/UserDetail';
import UserNew from './user/UserNew';
import UserEdit from './user/UserEdit';

class Home extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div>
        <h2>OK</h2>
      </div>
    );
  }
}

class TSGCoder extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <BrowserRouter>
        <div>
          <ul>
            <li><Link to="/">Home</Link></li>
            <li><Link to="/employees">employee</Link></li>
          </ul>
          <Route exact path="/" component={Home} />
          <Route exact path="/employees" component={UserList} />
          <Route exact path='/employees/:id([0-9]+)' component={UserDetail} />
          <Route exact path="/employees/new" component={UserNew} />
          <Route exact path="/employees/:id([0-9]+)/edit" component={UserEdit} />
        </div>
      </BrowserRouter>
    );
  }
}

export default TSGCoder;
